<template>
  <div id="goods">
    <div class="my-bar">
      <div class="my-bar-left">
        <div class="arrow-left-con">
          <van-icon name="arrow-left" @click.native="$router.go('-1')"/>
        </div>
      </div>
    </div>
    <van-swipe class="goods-swipe" :autoplay="3000">
      <!--<van-swipe-item v-for="item,index in swiperList" :key="thumb">-->
      <!--<img :src="item" width="100%">-->
      <!--</van-swipe-item>-->
      <img style="width: 375px;height: 375px;" :src="baner" alt="">
    </van-swipe>
    <van-cell-group>
      <van-cell>
        <div class="goods-title">{{title}}</div>
        <div class="goods-price">
          <del>￥{{delprice}}</del>
          <div><img src="../../static/img/yh.png"/><span>{{quanJine}}</span></div>
          <!--<span>已售{{goodsInfo.data.sales_num > 10000 ? (goodsInfo.data.sales_num /10000 + '万') : goodsInfo.data.sales_num }} 件</span>-->
          <span>已售{{yishou}} 件</span>
        </div>
        <div class="dsj">
          <span>￥{{nowPrice}}</span><span>到手价</span>
        </div>
      </van-cell>
      <div class="fxz">
        <div><span>分享赚</span><span>自购或分享，立赚</span><span>￥{{quanJine}}</span></div>
        <div><span>升级赚</span><span>升级团长，立赚</span><span>￥3.5</span></div>
      </div>
    </van-cell-group>
    <van-cell-group class="goods-cell-group">
      <van-cell title="查看商品详情(点击查看详情)" is-link @click="solide()"/>
    </van-cell-group>
    <div v-if="isShow">
      <div v-for="(item,index) in datasImg" :key="index">
        <img style="width: 100%;" :src='item' alt="">
      </div>
    </div>
    <div id="goodslist" v-if="show">
      <div class="my-sub-header">商品图片</div>
      <img v-for="item,index in goods.thumb" :src="item" :key="index" width="100%"/>
    </div>
    <van-goods-action>
      <van-goods-action-mini-btn v-on:click="collect" icon="share" v-clipboard:copy="url" v-clipboard:success="onCopy"
                                 v-clipboard:error="onError">
        收藏
      </van-goods-action-mini-btn>
      <!--<van-goods-action-mini-btn icon="cart" @click="onClickCart">-->
      <!--购物车-->
      <!--</van-goods-action-mini-btn>-->
      <van-goods-action-big-btn
        v-if="isHidn"
        v-clipboard:copy="url"
        v-clipboard:success="onCopy"
        v-clipboard:error="onError" v-on:click="copyUrl">
        复制淘口令
      </van-goods-action-big-btn>
      <van-goods-action-big-btn primary @click="skipTaoBao">
        领券
      </van-goods-action-big-btn>
    </van-goods-action>
    <!--<van-sku v-model="showBase" :sku="sku" :goods="goods" :goods-id="goodsId" />-->
    <div>
      <iframe v-if="details" id="test" frameborder="0" scrolling="auto" :src="'https://mdetail.tmall.com/templates/pages/desc?id='+goodsId"
              style="width: 100%;border: none;height: 2000px;"></iframe>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'goods',
    data() {
      return {
        isHidn: true,
        copyData:'',
        datasImg:[],
        details: true,
        showBase: false,
        goodsId: '',
        show: false,
        swiperList: [],
        url: '',
        baner: "",
        goods: {
          title: '美国伽力果（约680g/3个）',
          price: 2680,
          express: '免运费',
          remain: 19,
          thumb: [],
        },
        isShow:false,
        goodsInfo: {data: []},
        links:'',
        lined:'',
      }
    },
    created() {
      this.goodsId = this.$route.query.id//获取url里的id
      this.title = this.$route.query.title//获取url里的title
      this.delprice = this.$route.query.delprice//获取url里的原价
      this.nowPrice = this.$route.query.nowPrice//获取url里的现价
      this.quanJine = this.$route.query.quanJine//获取url里的优惠券金额
      this.yishou = this.$route.query.yishou//获取url里的已经出售件数
      this.baner = this.$route.query.images//获取url里的图片
      this.thumbs = this.$route.query.thumbs//获取url里的数据
      this.commission_moneys = this.$route.query.commission_moneys//获取url里的数据
      this.coupon_links = this.$route.query.coupon_links//获取url里的数据
      this.collection_ids = this.$route.query.collection_ids//获取url里的数据
      localStorage.setItem('tupian', this.thumbs)
      localStorage.setItem('goodsId', this.goodsId)
      localStorage.setItem('nowPrice', this.nowPrice)
      console.log(this.coupon_links,"444444444444444")
      this.copyPassword()
    },
    mounted() {
      this.addFootprint()
      if( localStorage.getItem('gID') == 21 ){
        this.links = this.coupon_links
        this.jddetails()
      }else if( localStorage.getItem('gID') == 31 ){
        this.links = this.coupon_links
        this.pdddetails()
      }
      if (localStorage.getItem('gID') == 21 || localStorage.getItem('gID') == 31 ) {
        this.isShow = true;
        this.details = false;
        this.isHidn = false;
      }else if( localStorage.getItem('gID') !=21 || localStorage.getItem('gID') != 31 ){
        this.isLink()
        this.isHidn = true;
        this.details = true;
        this.isShow = false;
      }
    },
    methods: {
      isLink() {
        let self = this
        let params = {
          itemid: this.goodsId,
        }
        this.$POST('/user/hcapi', params).then(res => {
          if (res.code === 200) {
            self.links = res.data
          } else if (res.code === 101) {
            console.log(res)
          }
        }).catch(err => {
          this.$toast(res.msg)
          console.log(err)
        })
      },
      // 复制到剪切板
      onCopy(e) {
        this.$toast('复制成功')
      },
      onError() {
        this.$toast('复制失败')
      },
      collect() {
        let params = {
          thumb: this.thumbs,
          title: this.title,
          coupon_money: this.quanJine,
          origin_price: this.delprice,
          coupon_price: this.nowPrice,
          commission_money: this.commission_moneys,
          coupon_link: this.coupon_links,
          origin_id: this.goodsId,
        }
        this.$POST('/user/scadd', params).then(res => {
          console.log(res)
          if (res.code === 200) {
            this.$toast("收藏成功")
          } else if (res.code === 101) {
            console.log(res)
            this.$toast("您已经收藏过此商品")
          }
        }).catch(err => {
          this.$toast(res.msg)
          console.log(err)
        })
      },
      //我的足迹接口
      addFootprint() {
        let param = {
          thumb: localStorage.getItem('tupian'),
          coupon_price: localStorage.getItem('nowPrice'),
          origin_id: localStorage.getItem('goodsId'),
        }
        this.$POST('/user/footprint', param, true).then(res => {
          console.log(res);
          if (res.code === 200) {

          }
        }).catch(res => {
          this.$toast('连接服务器失败!');
          console.log(res);
        })
      },
      goOrder() {
        this.$router.push("order");
      },
      formatPrice() {
        return '¥' + (this.goods.price / 100).toFixed(2);
      },
      onClickCart() {
        // this.$router.push('cart');
      },
      solide() {
        var $this = this;
        if ($this.show) {
          $this.show = false
        } else {
          $this.show = true
        }
      },
      receive() {
        this.url = this.coupon_links,
          window.location.href = this.url
      },
      //京东商品详情
      jddetails() {
        let self = this;
        let param = {
          goodsid: this.goodsId,
          haveNoToken: false,
        }
        this.$POST('/jd/goods-detail', param, true).then(res => {
          console.log(res);
          if (res.code === 200) {
            self.datasImg = res.data.imags
            self.links = res.data.coupon_link
          }
        }).catch(res => {
          this.$toast('连接服务器失败!');
          console.log(res);
        })
      },
      //拼多多商品详情
      pdddetails() {
        let self = this;
        let param = {
          goodsid: this.goodsId,
          haveNoToken: false,
        }
        this.$POST('/pdd/goods-detail', param, true).then(res => {
          console.log(res);
          if (res.code === 200) {
            self.datasImg = res.data.imags
          }
        }).catch(res => {
          this.$toast('连接服务器失败!');
          console.log(res);
        })
      },
      //生成淘宝口令
      copyPassword(){
        let self = this
        let params = {
          url: this.coupon_links,
        }
        this.$POST('/user/hcapi', params, true).then(res => {
          console.log(res);
          if (res.code === 200) {
            self.copyData = res.data.tbk_pwd
            self.nev_link = res.data.coupon_click_url
          }
        }).catch(res => {
          this.$toast('连接服务器失败!');
          console.log(res);
        })
      },
      //复制淘宝口令
      copyUrl() {
        console.log(this.copyData)
        const input = document.createElement('input')
        document.body.appendChild(input)
        input.setAttribute('value',this.copyData)//需要复制的内容
        input.select()
        if (document.execCommand('copy')) {
          document.execCommand('copy')
          this.$toast('已复制到粘贴板!');
        }
        document.body.removeChild(input)
      },
      //跳转淘宝
      skipTaoBao() {
        if (localStorage.getItem('gID')==11){
          this.lined = this.nev_link
        }else if (localStorage.getItem('gID')==31) {
          this.lined = this.links
        }
        // console.log(this.lined,"55555555")
        bridge.call('gotoTaobao',this.lined)
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #goods {
    padding-bottom: 50px;
  }

  .arrow-left-con {
    position: absolute;
    left: 15px;
    top: 15px;
    z-index: 2;
    width: 30px;
    height: 30px;
    border-radius: 30px;
    background: rgba(0, 0, 0, .3);
    line-height: 35px;
    color: #ffffff;
  }

  .goods-title {
    font-family: 黑体;
    font-size: 16px;
    font-weight: bold;
    width: 75%;
  }

  .goods-price {
    width: 100%;
    margin-top: 15px;
  }

  .goods-price > del {
    float: left;
    font-size: 16px;
  }

  .goods-price > span {
    float: left;
    color: #a8a9a9;
    font-size: 14px;
    margin-left: 18px;
  }

  .goods-price:after {
    content: "";
    width: 0;
    height: 0;
    clear: both;
    display: block;
  }

  .goods-price > div {
    position: relative;
    font-size: 14px;
    color: #000;
    width: 60px;
    height: 20px;
    text-align: right;
    float: left;
    margin-left: 18px;
  }

  .goods-price > div > span {
    position: absolute;
    right: 1px;
    top: -2px;
    z-index: 1;
    color: #ffffff;
    font-size: 14px;
    left: 10px;
  }

  .goods-price > div > img {
    display: block;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 0;
  }

  .dsj {
    margin-top: 15px;
  }

  .dsj > span {
    color: #f41e2e;
    font-size: 14px;
  }

  .dsj > span:first-child {
    font-size: 26px;
    color: #f41e2e;
    margin-right: 5px;
  }

  .fxz {
    width: 100%;
    background: #fffbff;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 14px;
    text-align: left;
  }

  .fxz > div {
    width: 85%;
    margin: 0 auto;
  }

  .fxz > div > span:first-child {
    display: inline-block;
    padding: 2px 8px;
    border: 1px solid #f41e2e;
    border-radius: 25px;
    color: #f41e2e;
    margin-right: 8px;
  }

  .fxz > div > span:last-child {
    color: #f41e2e;
  }

  .fxz > div:last-child {
    margin-top: 10px;
  }

  .fxz > div:last-child > span:first-child {
    border: 1px solid #000;
    color: #000;
  }

  .fxz > div:last-child > span:last-child {
    color: #f41e2e;
  }
</style>
<style>
  #goods .goods-cell-group {
    text-align: left;
  }
</style>



