import Vue from 'vue'
import Router from 'vue-router'
import home from '@/view/home'
import goods from '@/view/goods'


Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: '',
      component: home,
      children: [{
        path: '/',
        name: 'index',
        component: resolve => require(['@/view/index'], resolve),
        // meta: {
        //   requireAuth: true, // 添加该字段，表示进入这个路由是需要登录的
        // }
      }, {
        path: 'coupon',
        name: 'coupon',
        component: resolve => require(['@/view/coupon'], resolve)
      },{
        path: 'goods',
        name: 'goods',
        component: resolve => require(['@/view/havegoods'], resolve)
      },{
        path: 'circle',
        name: 'circle',
        component: resolve => require(['@/view/circle'], resolve)
      },{
        path: 'my',
        name: 'my',
        component: resolve => require(['@/view/my'], resolve)
      }]
    },
    {
      path: '/goodsdetail',
      name: 'goodsdetails',
      component: goods
    },
    {
      path: '/myroot',
      name: 'myroot',
      component: resolve => require(['@/view/myroot'], resolve)
    },
    {
      path: '/invation',
      name: 'invation',
      component: resolve => require(['@/view/invation'], resolve)
    },
    {
      path: '/myinvate',
      name: 'myinvate',
      component: resolve => require(['@/view/invate'], resolve)
    },
    {
      path: '/order',
      name: 'order',
      component: resolve => require(['@/view/order'], resolve)
    },
    {
      path: '/income',
      name: 'income',
      component: resolve => require(['@/view/income'], resolve)
    },
    {
      path: '/collection',
      name: 'collection',
      component: resolve => require(['@/view/collection'], resolve)
    },
    {
      path: '/service',
      name: 'service',
      component: resolve => require(['@/view/service'], resolve)
    },
    {
      path: '/login',
      name: 'login',
      component: resolve => require(['@/view/login'], resolve)
    },
    {
      path: '/register',
      name: 'register',
      component: resolve => require(['@/view/register'], resolve)
    },
    {
      path: '/forget',
      name: 'forget',
      component: resolve => require(['@/view/forget'], resolve)
    },
    {
      path: '/practice',
      name: 'practice',
      component: resolve => require(['@/view/practice'], resolve)
    },
    {
      path: '/chanelList',
      name: 'chanelList',
      component: resolve => require(['@/view/chanelList'], resolve)
    },
    {
      path: '/search',
      name: 'search',
      component: resolve => require(['@/view/search'], resolve)
    },
    {
      path: '/chanelList2',
      name: 'chanelList2',
      component: resolve => require(['@/view/chanelList2'], resolve)
    },
    {
      path: '/list',
      name: 'list',
      component: resolve => require(['@/view/list'], resolve)
    },
    {
      path: '/aboutUs',
      name: 'aboutUs',
      component: resolve => require(['@/view/aboutUs'], resolve)
    },
    {
      path: '/particulars',
      name: 'particulars',
      component: resolve => require(['@/view/particulars'], resolve)
    },
		{
			path:'/upname',
			name:'upname',
			component:resolve=> require(['@/view/upname'], resolve)
		},
		{
			path:'/purse',
			name:'purse',
			component:resolve=> require(['@/view/purse'], resolve)
		},
		{
			path:'/sign',
			name:'sign',
			component:resolve=> require(['@/view/sign'], resolve)
		},
		{
			path:'/record',
			name:'record',
			component:resolve=> require(['@/view/record'], resolve)
		},
  ]
})



