/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url,params={}){
  let toast = Toast.loading({mask: true,message: '加载中...'})
  return new Promise((resolve,reject) => {
    axios.get(url,{
      params:params
    })
      .then(response => {
        toast.clear()
        resolve(response.data);
      })
      .catch(err => {
        toast.clear()
        reject(err)
      })
  })
}

/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url,data = {},show = true){
  let toast
  if(show){
    toast = Toast.loading({mask: true,message: '加载中...'})
  }
  return new Promise((resolve,reject) => {
    axios.post(url,QS.stringify(data))
      .then(response => {
        if(show) {
          toast.clear()
        }
        resolve(response);
      },err => {
        if(show) {
          toast.clear()
        }
        reject(err)
      })
  })
}

/**
 * 封装patch请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.patch(url,data)
      .then(response => {
        resolve(response.data);
      },err => {
        reject(err)
      })
  })
}




